import React from "react";
import { motion } from "framer-motion"
import { Formik, Form } from 'formik';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
const baseUrl = "https://lilith-server.glitch.me"
export const RegistrationComponent = ({ formAccept, setFormAccept }) => {
    const sendEmail = async (values, { setSubmitting }) => {
        try {
            let dataSend = {
                name: values.name,
                telegram: values.telegram,
            };

            const res = await fetch(`${baseUrl}/email/sendEmail`, {
                method: "POST",
                body: JSON.stringify(dataSend),
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json",
                },
            });

            if (res.ok) {
                setFormAccept(true)
            } else {
                console.error(`: ${res.status} - ${res.statusText}`);
            }
        } catch (error) {
            console.error(":", error);
        } finally {
            setSubmitting(false);
        }
    };
    const validate = (values) => {
        const errors = {};

        // Validate name
        if (!values.name) {
            errors.name = '   ';
        } else if (!/^[A-Z-][a-z-]{0,14}$/.test(values.name)) {
            errors.name = '          15 ';
        }

        // Validate telegram
        if (!values.telegram) {
            errors.telegram = '   ';
        } else if (!/^@([a-z-0-9_]){1,}$/.test(values.telegram)) {
            errors.telegram = '    @    ';
        }

        return errors;
    };
    return (<div className="registration">
        <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.5, delay: 0.2 }}
            className="registration__container">
            <Formik validate={validate} onSubmit={sendEmail} initialValues={{ name: "", telegram: "" }}>
                {({
                    values,
                    errors,
                    touched,
                    handleChange,
                    handleBlur,
                    isSubmitting,
                    handleSubmit,
                }) => (
                    <Form style={{ height: "100%" }}>
                        <div className="registration-form">
                            <div className="registration-form__block">
                                <motion.h2
                                    initial={{ opacity: 0, x: 50 }}
                                    animate={{ opacity: 1, x: 0 }}
                                    transition={{ duration: 0.5, delay: 0.3 }}
                                    className="registration-form__title">
                                     
                                </motion.h2>
                                <motion.div
                                    initial={{ opacity: 0, x: 50 }}
                                    animate={{ opacity: 1, x: 0 }}
                                    transition={{ duration: 0.5, delay: 0.5 }}
                                    className="registration-form__registration-form">
                                    <TextField
                                        label=""
                                        variant="filled"
                                        className={formAccept ? "registration-form__field accept" : "registration-form__field registration-form__field-name"}
                                        type="name"
                                        name="name"
                                        color="warning"
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        value={values.name}
                                        error={touched.name && Boolean(errors.name)}
                                        helperText={touched.name && errors.name}
                                    />
                                    <TextField
                                        label=""
                                        variant="filled"
                                        className={formAccept ? "registration-form__field registration-form__field-name accept" : "registration-form__field registration-form__field-name"}
                                        type="telegram"
                                        name="telegram"
                                        color="warning"
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        value={values.telegram}
                                        error={touched.telegram && Boolean(errors.telegram)}
                                        helperText={touched.telegram && errors.telegram}
                                    />
                                    <Button
                                        type="submit"
                                        className={formAccept ? "registration-form__button accept" : "registration-form__button"}
                                        color={formAccept ? "success" : "warning"}
                                        variant="outlined"
                                        fullWidth
                                    >
                                        {isSubmitting ? '...' : formAccept ? "" : ''}
                                    </Button>
                                </motion.div>
                            </div>
                        </div>
                    </Form>
                )}
            </Formik>
        </motion.div>
    </div>)
}