import React from "react";
import { motion } from "framer-motion"
import { NavLink } from "react-router-dom"
export const HeaderComponent = () => {

    return (
        <header className="header">
            <div className="header__container">
                <div className="header-block">
                    <h1 className="header-block__title">
                        <motion.span
                            initial={{ opacity: 0, x: -100 }}
                            animate={{ opacity: 1, x: 0 }}
                            transition={{ duration: 0.5, delay: 0.3 }}>
                            LILITH
                        </motion.span>
                        <motion.span
                            initial={{ opacity: 0, x: -100 }}
                            animate={{ opacity: 1, x: 0 }}
                            transition={{ duration: 0.5, delay: 0.6 }}>
                            AGENCY
                        </motion.span>
                    </h1>
                    <motion.h2
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ duration: 1, delay: 0.9 }}
                        className="header-block__text">Beauty meets success</motion.h2>
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ duration: 1, delay: 1 }}>
                        <button className="header-block__reg-button">
                            <NavLink to="/req" className="header-block__reg-button-a" href="#"><span className="header-block__reg-button-span"> </span></NavLink>
                        </button>
                    </motion.div>
                </div>
            </div>
        </header >
    )
}