import React
, { useState, useEffect }
    from "react";
import logo from "./img/logo.svg";
import { motion } from "framer-motion";
import { NavLink } from "react-router-dom";
import gmail from "./img/gmail.svg"
import telegram from "./img/telegram.svg"
import { Link } from "react-scroll"
export const NavBarComponent = () => {
    const [scrolled, setScroll] = useState(0);

    useEffect(() => {
        const handleScroll = () => {
            setScroll(window.scrollY);
        };

        //     
        window.addEventListener("scroll", handleScroll);

        //       
        return () => {
            window.removeEventListener("scroll", handleScroll);
        };
    }, []);
    return (
        <div className={scrolled > 50 ? "navbar scrolled" : "navbar noscrolled"}>
            <div className={scrolled > 50 ? "navbar__container scrolled" : "navbar__container"}>
                <motion.div
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ duration: 1 }}
                    className="navbar__logo">
                    <NavLink to="/">
                        <img className="navbar__logo-svg" src={logo} alt="" />
                    </NavLink>
                </motion.div>
                <nav className="navbar__list">
                    <ul className="navbar__list-block">
                        <li className="navbar__list-elemet">
                            <Link
                                spy={true}
                                smooth={true}
                                offset={-50}
                                duration={500}
                                to="about"> </Link></li>
                        <li className="navbar__list-elemet"><Link
                            spy={true}
                            smooth={true}
                            offset={-50}
                            duration={500}
                            to="conditions"></Link></li>
                        <li className="navbar__list-elemet"><Link
                            spy={true}
                            smooth={true}
                            offset={-50}
                            duration={500}
                            to="feedback"></Link></li>
                    </ul>
                </nav>
                <div className="navbar__social">
                    <a href="https://t.me/lilith_agency"><img src={telegram} alt="" /></a>
                    <a href="mailto:Nonenone2411@gmail.com"><img src={gmail} alt="" /></a>
                </div>
                <button className="navbar__reg-button">
                    <NavLink to="/req" className="navbar__reg-button-a" href="#"><span className="navbar__reg-button-span"> </span></NavLink>
                </button>
            </div>
        </div>
    );
};
