import React, { useState } from "react";
import { FooterComponent } from "./components/FooterComponent";
import { HeaderComponent } from "./components/HeaderComponent";
import { MainComponent } from "./components/MainComponent";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { RegistrationComponent } from "./components/RegistraionComponent";
import { NavBarComponent } from "./components/NavBarComponent";
import "./styles/style.css";
function App() {
  const [formAccept, setFormAccept] = useState(false);

  return (
    <BrowserRouter>
      <NavBarComponent />
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/req" element={<RegistrationComponent formAccept={formAccept} setFormAccept={setFormAccept} />} />
      </Routes>
    </BrowserRouter>
  );
}
const HomePage = () => {
  return (<>
    <HeaderComponent />
    <MainComponent />
    <FooterComponent />
  </>)
}
export default App;
